    //
    getSign(obj = {}) {
        let { token } = user.methods.getUser()
        function sort(obj) {
            if (obj instanceof Array) {
                //,map
                obj = obj.map((ele, index) => {
                    if (ele instanceof Object) {
                        var newObj = {}
                        Object.keys(ele).sort().forEach(function (key) {
                            var o = ele[key]
                            if (o instanceof Object) {
                                o = sort(o)
                            }
                            newObj[key] = o
                        })
                        ele = newObj
                    }
                    return ele
                })
                return obj
            }

            var newObj = {}
            //ASCII
            //
            Object.keys(obj).sort((s1, s2) => {
                let x1 = s1.toUpperCase()
                let x2 = s2.toUpperCase()
                if (x1 < x2) {
                    return -1
                }
                if (x1 > x2) {
                    return 1
                }
                return 0
            }).forEach(function (key) {
                var o = obj[key]
                if (o instanceof Object) {
                    o = sort(o)
                }
                newObj[key] = o
            })
            return newObj
        }

        const sign_data = {
            Data: obj,
            Global: { IMEI: "", IMSI: "", IP: "", OS: 3, Sign: "", Token: token }
        }

        return {
            Data: obj,
            Global: {
                IMEI: "", IMSI: "", IP: "", OS: 3,
                Sign: md5(JSON.stringify(sort(sign_data)) + ')(4AzEdr5J6a`@#$*%'),
                Token: token
            }
        }
    },
    //
